#comp_Member {
	$scoreBoard-choseTable_backgroundColor: rgb(255, 255, 255);
	$scoreBoard-choseTable-option_backgroundColor: rgb(24, 24, 24);
	$scoreBoard-Table_backgroundColor: rgb(245, 250, 255);
	$scoreBoard-Table-head_backgroundColor: rgb(12, 2, 205);
	$scoreBoard-Table-caption_backgroundColor: rgb(0, 20, 110);
	$scoreBoard-Table-subject_backgroundColor: rgb(225, 255, 241);
	$scoreBoard-Table-DTBHK_backgroundColor: rgb(255, 239, 227);
	$userGroup_backgroundColor: linear-gradient(90deg, rgb(255, 255, 255), rgb(240, 254, 255));

	#userGroups {
		.userGroup {
			position: relative;
			height: 8%;
			display: flex;
			padding: 0.5% 2%;
			font-size: 0.9em;
			background-image: $userGroup_backgroundColor;
			box-shadow: var(--boxShadow);

			.box-img {
				position: relative;
				height: 100%;
				width: 10%;
				display: flex;
				align-items: center;

				img {
					object-fit: cover;
					height: auto;
					width: 100%;

					border-radius: 50%;
					border: calc(var(--fontsize) / 10) solid black;
				}
			}

			.box-text {
				font-size: 1em;
				position: relative;
				width: 90%;

				margin-left: 2%;

				.Name-Type {
					font-size: 1em;
					color: var(--textcolor1);
				}

				.OtherProps {
					font-size: 0.8em;
					color: var(--textcolor1);

					list-style: none;
				}
			}
		}

		.userGroup + .userGroup {
			margin-top: 1.5em;
		}
	}
	#userRecentActs {
		.userRecentAct {
		}

		.userRecentAct + .userRecentAct {
			margin-top: 1.5em !important;
		}
	}
	#userScoreBoard {
		position: relative;
		font-size: 1em;

		#lableChoseTable {
			position: relative;
			display: flex;
			justify-content: right;
			align-items: center;
			font-size: 1em;

			#choseTable {
				position: relative;
				width: 18%;
				font-size: 1em;
				background-color: $scoreBoard-choseTable_backgroundColor;
				border: 0.05em solid black;
				text-align: center;
				text-wrap: nowrap;
				padding: 0.1% 1%;
				color: var(--textcolor1);
				transition:  background_color 0.2s, color 0.2s;

				svg{
					margin-left: 1em;
				}
			}

			#choseTable:hover{
				cursor: pointer;
				background-color: rgb(180, 180, 180);
				color: var(--textcolor2);
				transform: scale(1);
			}

			div[data-tippy-root]{
				z-index: 1 !important;
			}

			#list_HK{
				bottom: 0;
				display: flex;
				flex-direction: column;
				height: max-content;
				width: 11em;
				background-color: rgb(255, 255, 255);
				box-shadow: var(--boxShadow);
				border: 0.05em solid black;
				border-radius: 0.1vw;
				padding: 0.25vw 0.5vw;

				span{
					text-align: center;
					color: var(--textcolor1);
				}

				span:hover{
					cursor: pointer;
					background-color: rgb(240, 240, 240, 0.5);
				}

				span:active{
					background-color: rgb(230, 230, 230, 1);
				}
			}
		}

		h5 {
			font-size: 1.3em;
			text-align: center;
			font-weight: 600;
			background-color: $scoreBoard-Table-caption_backgroundColor;
			color: var(--textcolor2);
			padding: 0.5% 0;
			margin-bottom: 0.6em;
		}

		table {
			width: 100%;
			border-spacing: 0em;
			background-color: $scoreBoard-Table_backgroundColor;

			thead {
				color: var(--textcolor2);

				tr {
					position: relative;
					background-color: $scoreBoard-Table-head_backgroundColor;
					padding: 0.5% 1%;

					th {
						font-weight: 500;
						border: 0.05em solid rgb(0, 0, 0);
					}

					#scoreBoard-subject {
						width: 20%;
					}
				}
			}

			tbody {
				td {
					border: 0.05em solid black;
					font-weight: 350;
				}

				.TX-score{
					span{
						margin: 0 0.3em;
					}
				}

				.subject {
					font-weight: 400;
					background-color: $scoreBoard-Table-subject_backgroundColor;
				}

				.DTBHK-score {
					background-color: $scoreBoard-Table-DTBHK_backgroundColor;
				}
			}

			th,
			td {
				text-align: center;
			}
		}
	}
}
