#comp_Exam {
	position: relative;
	display: flex;
	justify-content: center;
	font-size: var(--fontsize);
	background-color: rgb(245, 245, 255);

	$container_backgroundColor: rgb(255, 255, 255);
	$searchForm_backgroundColor: rgb(255, 255, 255);
	$exam_backgroundColor: rgb(210, 230, 255);
	$exam-icon_backgroundColor: rgb(0, 30, 115);

	#UNDERICON {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;

		img {
			width: 35%;
		}
	}

	#examContainer {
		position: relative;
		width: 80%;
		background-color: $container_backgroundColor;
		box-shadow: var(--boxShadow);
		font-size: 1em;
		padding: 0.5% 1%;

		#searchExam {
			display: flex;
			justify-content: center;
			align-items: center;

			font-size: 1em;
			margin-top: 1em;

			#searchForm {
				width: 30%;
				background-color: $searchForm_backgroundColor;
				border: 0.1vw solid black;
				border-radius: 0.2vw;
			}

			.searchField {
				font-size: 1em;
			}
		}

		#listExams {
			position: relative;
			font-size: 1em;
			padding: 1% 2%;
			padding-bottom: var(--paddingbottom);
			height: 80vh;
			overflow-y: auto;
			ul {
				display: flex;
				flex-wrap: wrap;
				list-style: none;
				font-size: 1em;

				a.exam {
					position: relative;
					display: flex;
					width: 24%;
					margin: 0.5% calc(4% / 8);
					padding: 0.25% 1%;

					border-radius: 0.2vw;
					border: 0.07vw solid black;

					background-color: $exam_backgroundColor;
					font-size: 1em;
					.exam-icon {
						position: relative;
						width: 10%;
						display: flex;
						justify-content: center;
						align-items: center;
						font-size: 2em;

						margin-right: 0.5em;
						color: $exam-icon_backgroundColor;
					}

					.exam-intro {
						width: 90%;
						display: flex;
						flex-direction: column;
						font-size: 0.8em;
						color: var(--textcolor1);

						* {
							text-wrap: nowrap;
							overflow-x: hidden;
							text-overflow: ellipsis;
						}
					}
				}
			}
		}
	}
}
